/* === GLOBAL RESET === */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Arial, sans-serif;
  background: #fff;
  color: #111827;
}

.min-h-screen {
  min-height: 25vh;

}

/* === COLOR PALETTE === */
.text-brown-800 { color: #4a3a1f; }
.text-brown-600 { color: #d4cdc4; }
.text-gold-800  { color: #ead59f; }
.text-white     { color: #fff; }
.text-gray-900  { color: #4a3a1f; }

/* === SPACING & TYPOGRAPHY === */
.py-20 { padding: 5rem 0; }
.py-16 { padding: 4rem 0; }
.px-6  { padding: 0 1.5rem; }

.max-w-7xl { max-width: 80rem; margin: 0 auto; 
  padding: 10px; }

.max-w-4xl { max-width: 56rem; }
.max-w-3xl { max-width: 48rem; }

.text-center { text-align: center; }
.font-bold   { font-weight: 700; }

.text-5xl { font-size: 3rem; line-height: 1.1; }
.text-4xl { font-size: 2.25rem; line-height: 1.2; }
.text-xl  { font-size: 1.25rem; line-height: 1.75rem; }
.text-lg  { font-size: 1.125rem; line-height: 1.75rem; }

.mb-6  { margin-bottom: 1.5rem; }
.mb-12 { margin-bottom: 3rem; }
.mx-auto { margin-left: auto; margin-right: auto; }

/* === TRANSITIONS === */
.transition-all { transition: all 0.3s ease; }
.duration-1000  { transition-duration: 1s; }
.delay-300      { transition-delay: 0.3s; }

.opacity-0   { opacity: 0; }
.opacity-100 { opacity: 1; }

.translate-y-0     { transform: translateY(0); }
.translate-y-10    { transform: translateY(2.5rem); }
.-translate-y-10   { transform: translateY(-2.5rem); }

.overflow-hidden { overflow: hidden; }

/* === GRADIENT BACKGROUNDS === */
.bg-gradient-to-br,
.from-gold-600,
.to-brown-800 {
  background: linear-gradient(135deg, #d4a650 0%, #8b6914 100%);
}

/* === TAB NAVIGATION === */
.tabs-container {
  background: #fff;
  border-bottom: 2px solid #e5dcc8;
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: 0 2px 12px rgba(139, 105, 20, 0.1);
}

.tabs-wrapper {
  display: flex;
  gap: 0;
  overflow-x: auto;
  scrollbar-width: none;
}

.tabs-wrapper::-webkit-scrollbar {
  display: none;
}

.tab {
  padding: 1.2rem 2rem;
  font-size: 1rem;
  font-weight: 600;
  color: #6e5230;
  background: none;
  border: none;
  border-bottom: 3px solid transparent;
  cursor: pointer;
  transition: all 0.3s;
  white-space: nowrap;
  outline: none;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.tab-icon {
  width: 1.25rem;
  height: 1.25rem;
}

.tab:hover {
  color: #8b6914;
  background: #faf7f0;
}

.tab.active {
  color: #8b6914;
  border-bottom-color: #d4a650;
  background: #fef9ed;
}

/* === SERVICE SECTIONS === */
.services-container {
  position: relative;
  overflow: hidden;
}

.service-section-fullscreen {
  min-height: 100vh;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.service-content-overlay {
  background: rgba(139, 105, 20, 0.15);
  backdrop-filter: blur(2px);
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4rem 0;
}

.service-fullscreen-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  max-width: 1200px;
  margin: 0 auto;
  align-items: center;
}

.service-header-fullscreen {
  text-align: left;
  color: #fff;
}

.icon-bg-fullscreen {
  background: rgba(212, 166, 80, 0.25);
  backdrop-filter: blur(10px);
  padding: 1.5rem;
  border-radius: 1rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;
}

.service-icon-fullscreen {
  width: 3rem;
  height: 3rem;
  color: #fef9ed;
}

.service-title-fullscreen {
  font-size: 3.5rem;
  font-weight: 700;
  color: #fef9ed;
  margin-bottom: 1.5rem;
  text-shadow: 2px 2px 4px rgba(74, 58, 31, 0.5);
}

.service-description-fullscreen {
  font-size: 1.5rem;
  color: #fef9ed;
  line-height: 1.7;
  text-shadow: 1px 1px 2px rgba(74, 58, 31, 0.4);
}

.service-steps-container {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  padding: 2.5rem;
  border-radius: 1.5rem;
  box-shadow: 0 20px 40px rgba(139, 105, 20, 0.3);
}

.steps-title {
  font-size: 1.75rem;
  font-weight: 700;
  color: #6e5230;
  margin-bottom: 1.5rem;
  text-align: center;
}

.service-steps-fullscreen {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.service-step-fullscreen {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  background: #fff;
  padding: 1.25rem;
  border-radius: 0.75rem;
  box-shadow: 0 2px 8px rgba(139, 105, 20, 0.1);
  transition: transform 0.2s, box-shadow 0.2s;
}

.service-step-fullscreen:hover {
  transform: translateX(5px);
  box-shadow: 0 4px 12px rgba(212, 166, 80, 0.25);
}

.step-icon-fullscreen {
  width: 1.5rem;
  height: 1.5rem;
  color: #d4a650;
  flex-shrink: 0;
  margin-top: 0.125rem;
}

.step-text-fullscreen {
  color: #4a3a1f;
  font-size: 1rem;
  line-height: 1.6;
  font-weight: 500;
}

/* === WHY CHOOSE US === */
.bg-gray-50 {
  background: #faf7f0;
}

.why-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}

.why-card {
  background: #fff;
  padding: 2rem;
  border-radius: 0.75rem;
  box-shadow: 0 1px 3px rgba(139, 105, 20, 0.15);
}

.why-icon-bg {
  background: #fef9ed;
  width: 3rem;
  height: 3rem;
  border-radius: 0.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
}

.why-icon {
  width: 1.5rem;
  height: 1.5rem;
  color: #d4a650;
}

.why-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: #4a3a1f;
  margin-bottom: 0.75rem;
}

.why-description {
  color: #6e5230;
  line-height: 1.6;
}

/* === CTA SECTION === */
.cta-section {
  background: linear-gradient(135deg, #8b6914 0%, #d4a650 100%);
  color: #fff;
  padding: 4rem 1.5rem;
  text-align: center;
}

.cta-title {
  font-size: 2.25rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  color: #fef9ed;
}

.cta-text {
  font-size: 1.25rem;
  color: #fef9ed;
  margin-bottom: 2rem;
}

.cta-btn {
  background: #fff;
  color: #8b6914;
  padding: 1rem 2rem;
  border-radius: 0.75rem;
  font-size: 1.125rem;
  font-weight: 600;
  border: none;
  cursor: pointer;
  outline: none;
  transition: background 0.2s, color 0.2s;
}

.cta-btn:hover {
  background: #fef9ed;
  color: #6e5230;
}

/* === RESPONSIVE STYLES === */
@media (max-width: 1024px) {
  .why-grid {
    grid-template-columns: 1fr;
  }

  .service-fullscreen-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
}

@media (max-width: 768px) {
  .text-5xl {
    font-size: 2rem;
  }

  .text-4xl {
    font-size: 1.75rem;
  }

  .service-title-fullscreen {
    font-size: 2.5rem;
  }

  .service-description-fullscreen {
    font-size: 1.25rem;
  }

  .service-steps-container {
    padding: 1.5rem;
  }

  .tab {
    font-size: 0.875rem;
    padding: 1rem;
  }

  .service-section-fullscreen {
    background-attachment: scroll;
  }
}
@media (max-width: 480px) {
  .text-5xl {
    font-size: 1.75rem;
  }

  .text-4xl {
    font-size: 1.5rem;
  }

  .text-xl {
    font-size: 1.125rem;
  }

  .text-lg {
    font-size: 1rem;
  }

  .py-20 {
    padding: 3rem 0;
  }

  .py-16 {
    padding: 2rem 0;
  }

  .px-6 {
    padding: 0 1rem;
  }

  .service-title-fullscreen {
    font-size: 2rem;
  }

  .service-description-fullscreen {
    font-size: 1rem;
  }

  .service-steps-container {
    padding: 1rem;
  }

  .tab {
    font-size: 0.8rem;
    padding: 0.75rem 1rem;
  }

  .why-grid {
    grid-template-columns: 1fr;
    gap: 1.25rem;
  }

  .why-card {
    padding: 1.5rem;
  }

  .cta-title {
    font-size: 1.75rem;
  }

  .cta-text {
    font-size: 1rem;
  }

  .cta-btn {
    font-size: 1rem;
    padding: 0.75rem 1.5rem;
  }

  .service-fullscreen-content {
    gap: 1.5rem;
    padding: 0 1rem;
  }

  .step-text-fullscreen {
    font-size: 0.95rem;
  }

  .service-step-fullscreen {
    flex-direction: column;
    align-items: flex-start;
  }

  .tabs-wrapper {
    padding: 0 0.5rem;
  }
}