/* === GLOBAL STYLES === */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Arial, sans-serif;
  background: #fff;
  color: #4a3a1f;
}

/* === HERO SECTION === */
.career-hero {
  background: linear-gradient(135deg, #8b6914 0%, #d4a650 100%);
  color: #fff;
  padding: 5rem 1.5rem;
  text-align: center;
}

.hero-title {
  font-size: 3.5rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  opacity: 0;
  transform: translateY(-30px);
  transition: all 1s ease;
}

.hero-title.loaded {
  opacity: 1;
  transform: translateY(0);
}

.hero-subtitle {
  font-size: 1.25rem;
  color: #fef9ed;
  max-width: 48rem;
  margin: 0 auto;
  line-height: 1.7;
  opacity: 0;
  transform: translateY(30px);
  transition: all 1s ease 0.3s;
}

.hero-subtitle.loaded {
  opacity: 1;
  transform: translateY(0);
}

/* === TAB NAVIGATION === */
.tabs-container {
  background: #fff;
  border-bottom: 2px solid #e5dcc8;
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: 0 2px 12px rgba(139, 105, 20, 0.1);
}

.tabs-wrapper {
  display: flex;
  gap: 0;
  overflow-x: auto;
  scrollbar-width: none;
}

.tabs-wrapper::-webkit-scrollbar {
  display: none;
}

.tab {
  padding: 1.2rem 2rem;
  font-size: 1rem;
  font-weight: 600;
  color: #6e5230;
  background: none;
  border: none;
  border-bottom: 3px solid transparent;
  cursor: pointer;
  transition: all 0.3s;
  white-space: nowrap;
  outline: none;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.tab-icon {
  width: 1.25rem;
  height: 1.25rem;
}

.tab:hover {
  color: #8b6914;
  background: #faf7f0;
}

.tab.active {
  color: #8b6914;
  border-bottom-color: #d4a650;
  background: #fef9ed;
}

/* === FULL SCREEN SECTIONS === */
.career-section-fullscreen {
  min-height: 100vh;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.after-10th-section {
  background-image: linear-gradient(rgba(212, 166, 80, 0.7), rgba(139, 105, 20, 0.7)),
  url("https://education.sakshi.com/sites/default/files/images/2022/04/07/10th-exams-1649333143.jpg");
}

.after-12th-section {
  background-image: linear-gradient(rgba(212, 166, 80, 0.7), rgba(139, 105, 20, 0.7)),
  url("https://imagesvs.oneindia.com/ph-big/2020/06/students-appear-in-remaining-12th-examination-conducted-by-madhya-pradesh-board-of-secondary-edu_159186152520.jpg");
}

.enquiry-section {
  background-image: linear-gradient(rgba(212, 166, 80, 0.7), rgba(139, 105, 20, 0.7)),
  url("https://media.istockphoto.com/id/1398473177/photo/questionnaire-with-checkboxes-filling-survey-form-online-answer-questions.jpg?s=612x612&w=0&k=20&c=sgZY6ojUqB0goVyn_9fKLfeyZ6lyWjSb3-FQjgeUPec=");
}

.career-content-overlay {
  background: rgba(139, 105, 20, 0.15);
  backdrop-filter: blur(2px);
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4rem 0;
}

.career-fullscreen-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
}

/* === HEADER CONTENT === */
.career-header-fullscreen {
  text-align: left;
  color: #fff;
}

.icon-bg-fullscreen {
  background: rgba(212, 166, 80, 0.25);
  backdrop-filter: blur(10px);
  padding: 1.5rem;
  border-radius: 1rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;
}

.career-icon-fullscreen {
  width: 3rem;
  height: 3rem;
  color: #fef9ed;
}

.career-title-fullscreen {
  font-size: 3.5rem;
  font-weight: 700;
  color: #fef9ed;
  margin-bottom: 1.5rem;
  text-shadow: 2px 2px 4px rgba(74, 58, 31, 0.5);
}

.career-description-fullscreen {
  font-size: 1.5rem;
  color: #fef9ed;
  line-height: 1.7;
  text-shadow: 1px 1px 2px rgba(74, 58, 31, 0.4);
}

/* === CONTENT CARD === */
.career-content-card {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  padding: 2.5rem;
  border-radius: 1.5rem;
  box-shadow: 0 20px 40px rgba(139, 105, 20, 0.3);
  max-height: 80vh;
  overflow-y: auto;
}

.content-card-title {
  font-size: 1.75rem;
  font-weight: 700;
  color: #6e5230;
  margin-bottom: 1.5rem;
  text-align: center;
}

.career-options-grid {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.career-option-item {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  background: #fff;
  padding: 1.25rem;
  border-radius: 0.75rem;
  box-shadow: 0 2px 8px rgba(139, 105, 20, 0.1);
  transition: transform 0.2s, box-shadow 0.2s;
}

.career-option-item:hover {
  transform: translateX(5px);
  box-shadow: 0 4px 12px rgba(212, 166, 80, 0.25);
}

.option-icon {
  width: 1.5rem;
  height: 1.5rem;
  color: #d4a650;
  flex-shrink: 0;
  margin-top: 0.125rem;
}

.career-option-item h4 {
  font-size: 1.125rem;
  font-weight: 600;
  color: #4a3a1f;
  margin-bottom: 0.25rem;
}

.career-option-item p {
  font-size: 0.95rem;
  color: #6e5230;
  line-height: 1.5;
}

.guidance-note {
  background: #fef9ed;
  padding: 1.25rem;
  border-radius: 0.75rem;
  border-left: 4px solid #d4a650;
  color: #8b6914;
  font-size: 1rem;
  line-height: 1.6;
}
/* === ENQUIRY FORM === */
.enquiry-form-container {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  padding: 2.5rem;
  border-radius: 1.5rem;
  box-shadow: 0 20px 40px rgba(139, 105, 20, 0.3);
  max-width: 600px;
}

.enquiry-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  font-size: 1rem;
  font-weight: 600;
  color: #6e5230;
}

.form-group input,
.form-group select,
.form-group textarea {
  padding: 0.875rem;
  border: 2px solid #e5dcc8;
  border-radius: 0.5rem;
  font-size: 1rem;
  font-family: inherit;
  transition: border-color 0.2s;
  outline: none;
  background: #fff;
  color: #4a3a1f;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  border-color: #d4a650;
}

.form-group textarea {
  resize: vertical;
  min-height: 100px;
}

.submit-btn {
  background: #d4a650;
  color: #fff;
  padding: 1rem 2rem;
  border: none;
  border-radius: 0.5rem;
  font-size: 1.125rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
  outline: none;
}

.submit-btn:hover {
  background: #8b6914;
}
/* === WHY CHOOSE US === */
.why-choose-section {
  background: #faf7f0;
  padding: 4rem 0;
}

.section-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #4a3a1f;
  text-align: center;
  margin-bottom: 3rem;
}

.why-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}

.why-card {
  background: #fff;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: 0 2px 8px rgba(139, 105, 20, 0.15);
  text-align: center;
}

.why-icon-bg {
  background: #fef9ed;
  width: 4rem;
  height: 4rem;
  border-radius: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 1.5rem;
}

.why-icon {
  width: 2rem;
  height: 2rem;
  color: #d4a650;
}

.why-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #4a3a1f;
  margin-bottom: 0.75rem;
}

.why-description {
  color: #6e5230;
  line-height: 1.6;
  font-size: 1rem;
}
/* === RESPONSIVE STYLES === */

/* Tablets and below */
@media (max-width: 768px) {
  .hero-title {
    font-size: 2.5rem;
  }

  .hero-subtitle {
    font-size: 1rem;
    padding: 0 1rem;
  }

  .career-fullscreen-content {
    grid-template-columns: 1fr;
    gap: 2rem;
    padding: 0 1rem;
  }

  .career-title-fullscreen {
    font-size: 2.5rem;
  }

  .career-description-fullscreen {
    font-size: 1.125rem;
  }

  .content-card-title {
    font-size: 1.5rem;
  }

  .career-content-card {
    padding: 2rem;
    max-height: none;
  }

  .why-grid {
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem;
  }

  .section-title {
    font-size: 2rem;
  }
}

/* Mobile phones */
@media (max-width: 480px) {
  .hero-title {
    font-size: 2rem;
  }

  .hero-subtitle {
    font-size: 0.95rem;
  }

  .tab {
    padding: 1rem;
    font-size: 0.9rem;
  }

  .career-title-fullscreen {
    font-size: 2rem;
  }

  .career-description-fullscreen {
    font-size: 1rem;
  }

  .career-content-card {
    padding: 1.5rem;
  }

  .why-grid {
    grid-template-columns: 1fr;
  }

  .why-title {
    font-size: 1.25rem;
  }

  .why-description {
    font-size: 0.95rem;
  }

  .submit-btn {
    font-size: 1rem;
    padding: 0.75rem 1.5rem;
  }
}